import React, { useState, useEffect } from "react";
import GridView from "../grid-view/GridView";
import { Button, Col, Row } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

const pageSize = 10;

function ListContact() {
  const columns = [
    {
      label: "",
      id: "name",
      search: true,
    },
    {
      label: " ",
      id: "family",
      search: true,
    },
    {
      label: "",
      id: "mobile",
      search: true,
    },
    {
      label: " ",
      id: "hotkey",
      search: true,
    },
    {
      label: "",
      id: "enable",
      value: (value) => {
        return value ? "" : " ";
      },
    },
    {
      label: "",
      id: "id",
      value: (value) => {
        return (
          <Button
            onClick={() => {
              Edit(value);
            }}
          >
            
          </Button>
        );
      },
    },
    {
      label: "",
      id: "id",
      value: (value) => {
        return (
          <Button
            variant="danger"
            onClick={() => {
              Delete(value);
            }}
          >
            
          </Button>
        );
      },
    },
  ];

  const [state, setState] = useState({ content: [] });
  const [searchParams] = useState({});

  const navigate = useNavigate();

  const getData = (page = 0, size = pageSize) => {
    const searchUrl = new URLSearchParams(searchParams).toString();
    window.global
      .get(`/contacts/get?page=${page}&size=${size}&${searchUrl}`)
      .then((data) => {
        setState(data);
      })
      .catch((error) => {
        toast.error(
          "            " +
            "\n" +
            error
        );
      });
  };

  const Delete = (id) => {
    if (window.confirm("     ")) {
      window.global.post("/contacts/delete", { id: id }).then((result) => {
        if (result.delete) {
          toast.success("    ");
          getData();
        } else {
          toast.success("      ");
        }
      });
      getData();
    }
  };

  const Edit = (id) => {
    navigate("/contact/edit/" + id);
  };

  useEffect(() => {
    getData();
  });

  const search = (f, v) => {
    searchParams[f] = v;
    getData();
  };

  return (
    <div>
      <Row>
        <Col>
          <Button as={Link} to="/contact/create">
              
          </Button>
        </Col>
      </Row>
      <hr />
      <Row>
        <Col>
          <h4>   </h4>
        </Col>
      </Row>
      <Row>
        <Col>
          <GridView
            Columns={columns}
            Data={state.content}
            Events={{
              first: () => {
                getData(0);
              },
              pre: () => {
                if (state.pageable?.pageNumber - 1 >= 0)
                  getData(state.pageable?.pageNumber - 1);
              },
              next: () => {
                if (state?.totalPages > state.pageable?.pageNumber + 1)
                  getData(state.pageable?.pageNumber + 1);
              },
              last: () => {
                getData(state?.totalPages - 1);
              },
            }}
            Pagination={{
              totalElements: state?.totalElements,
              totalPages: state?.totalPages,
              size: state.pageable?.pageSize,
              offset: state.pageable?.offset,
              pageNumber: state.pageable?.pageNumber,
            }}
            Search={search}
          />
        </Col>
      </Row>
    </div>
  );
}

export default ListContact;
